import Header from "./Header";
import Grocery from "./Grocery";
import Meat from "./Meat";
import Produce from "./Produce";
import Dairy from "./Dairy";
import AddForm from "./AddForm";
import Frozen from "./Frozen";
import Other from "./Other";
import Empty from "./Empty";

import { useEffect, useState } from "react";
import { v4 as uuid_v4 } from "uuid";




const Main = () => {
  //Set Items
  const [items, setItems] = useState([]);

  //check Item
  const checkItem = (e) => {
    let item = e.currentTarget.nextSibling;
    item.classList.toggle("crossed");
    e.currentTarget.classList.toggle("checked");
    let newArr = [];

    
    const toggleCheck = () => {
      items.forEach((x) => {
        if (x.name === item.innerHTML) {
          let check = x.checked
          x.checked = (check === "false") ? "true" : "false";
        }
        newArr.push(x)
      });
    }

    toggleCheck();

    setItems(newArr);
    localStorage.setItem("groceryList", JSON.stringify(newArr));
  };


  //Remove Item
  const removeItem = (e) => {
    let item = e.currentTarget.previousSibling.innerHTML;
    let newArr = items.filter((x) => x.name !== item);

    setItems(newArr);
    localStorage.setItem("groceryList", JSON.stringify(newArr));
  };

  //Remove all checked
  const removeAllCrossed = () => {
    let newArr = items.filter(x => x.checked === "false");
    
    setItems(newArr);
    localStorage.setItem("groceryList", JSON.stringify(newArr));
  };

  //Clean Array
  const cleanArray = (oldArr) => {
    if (!oldArr) return;
    let newArr = new Set();
    
    Array.from(oldArr).forEach((x) => {
        newArr.add(JSON.stringify(x));
    });

    return [...newArr].map((x) => {
      if (typeof x === "string") return JSON.parse(x);
      else if (typeof x === "object") return x;
    });
  };

  const updateList = (newItems) => {
    if (newItems === null || newItems === undefined) return;
    let list = JSON.parse(localStorage.getItem("groceryList"));
    if (list === newItems) {
      setItems(newItems);
      return;
    }

    let clean = cleanArray(list);

    let newList = list
      ? JSON.stringify([...clean, newItems])
      : JSON.stringify(newItems);

    localStorage.setItem("groceryList", newList);

    list ? setItems(cleanArray([...clean, newItems])) : setItems(newItems);
  };
  //Show List on Load
  useEffect(() => {
    let stuff = JSON.parse(localStorage.getItem("groceryList"));

    if (stuff) {
      let storage = Array.from(stuff)
      setItems(cleanArray(storage));
    }
    
  }, []);

  //Add Items
  const addItems = (e) => {
    e.preventDefault();

    let [form, input, radio] = [
      document.querySelector(".form"),
      document.querySelector(".form__item").value,
      document.querySelector('input[name="department"]:checked').value,
    ];
    let newItems = { name: input, dept: radio, checked: "false" };

    updateList(newItems);
    form.reset();
  };

  const validateSubmit = () => {
    if (document.querySelector(".form__item").value === "") {
      document.querySelector(".form__submit").disabled = true;
    } else {
      document.querySelector(".form__submit").disabled = false;
    }
  };

  const filterList = (items, department) => {
    const isDept = (item) => item.dept === department;
    let filtered = items.filter(isDept);

    

    let list = filtered.map((item) => {
      let ischecked = (item.checked === 'true');
      return (
        <div key={uuid_v4()} className="item__container">
          <div
            className={ischecked ? "item__check checked" : "item__check"}
            onClick={checkItem}
          >
            <i className="fa fa-check item__check__check"></i>
          </div>
          <div
            key={uuid_v4()}
            className={ischecked ? "item__name crossed" : "item__name"}
          >
            {item.name}
          </div>
          <button className="item__btn" onClick={removeItem}>
            <i className="fas fa-times"></i>
          </button>
        </div>
      );
    });

    return list;
  };

  return (
    <>
      <Header removeAllCrossed={removeAllCrossed} />
      <AddForm addItems={addItems} validate={validateSubmit} />
      <Produce items={items} filterList={filterList} />
      <Meat items={items} filterList={filterList} />
      <Dairy items={items} filterList={filterList} />
      <Grocery items={items} filterList={filterList} />
      <Other items={items} filterList={filterList} />
      <Frozen items={items} filterList={filterList} />
      <Empty items={items} />
    </>
  );
}

export default Main
