

const AddForm = ({addItems, validate}) => {
    return (
      <>
        <form className="form hide" onSubmit={addItems}>
          <input type="text" className="form__item" onKeyUp={ validate } placeholder='add item...'/>
          <button  className="form__submit" onClick={addItems}  disabled>
            <i className="fas fa-plus"></i>
          </button>

          <input type="radio" id="produce" name="department" value="Produce" />
          <label htmlFor="produce">Produce</label>

          <input type="radio" id="dairy" name="department" value="Dairy" />
          <label htmlFor="dairy">Dairy</label>

          <input type="radio" id="meat" name="department" value="Meat" />
          <label htmlFor="meat">Meat</label>

          <input type="radio" id="Grocery" name="department" value="Grocery" />
          <label htmlFor="Grocery">Grocery</label>

          <input
            type="radio"
            id="Frozen"
            name="department"
            value="Frozen"
          />
          <label htmlFor="Frozen">Frozen</label>

          <input type="radio" id="other" name="department" value="Other" defaultChecked/>
          <label htmlFor="other">Other</label>
        </form>
      </>
    );
};

export default AddForm
